<template>
    <div class="pricticeDetails">
        <div class="header">
            <span @click="back" class="back">
                <img src="../assets/img/back.png">
            </span>
            <span class="usersTitle">修炼详情</span>
            <span class="share" @click="share">
                <img src="../assets/img/share.png">
            </span>
        </div>
        <div class="user">
            <div class="userName">Dear&ensp;李宁</div>
        </div>
        <div class="pricticeInfo">
            <span class="days">
                <span class="num">0</span>
                <span class="time">天</span>
                <br>
                <span class="detail">累计修炼</span>
            </span>
            <span class="minutes">
                <span class="num">768</span>
                <span class="time">分钟</span>
                <br>
                <span class="detail">修炼总时长</span>
            </span>
        </div>
    <div class="border"></div>
    <div class="pricticeData">
        <div class="title">修炼数据</div>
        <div class="dataHead">
            <span class="back" style="transform: scale(0.8);">
                <img src="../assets/img/back.png">
            </span>
            <span>本周</span>
            <span></span>
        </div>
    </div>
    <div class="dataArea">
        <div class="unit">单位：分钟</div>
        <ul class="data">
            <li class="dataNum" :style="{height: data.time * 0.08889 + 'rem', left: index * 1.43 + 0.6 + 'rem'}" v-for="(data, index) in thisweek" :key="index">
                <span class="dataCount">{{ data.time }}</span>
            </li>
        </ul>
    </div>
    <ul class="weeks">
        <li class="week" v-for="(day, index) in days" :key="index">{{ new Date().getDay() === day.num  ? '今天' : day.day}}</li>
    </ul>
    <div class="border"></div>
    <div class="allRecord">
        <div class="title">全部记录</div>
        <div class="record">
            <div class="content">
                <div class="contentTitle">六式基本功法</div>
                <div class="pricticeTime"></div>
                <div class="totalTime">
                    <span>已修习4天888分钟</span>
                    <span class="goPrictice">
                        <span class="orange">去修炼</span>
                        <span class="goIcon" style="transform: scale(0.8)">
                            <img src="../assets/img/goIcon.png">
                        </span>
                    </span>
                </div>
            </div>
        </div>
        <div class="record">
            <div class="content">
                <div class="contentTitle">二十四式</div>
                <div class="pricticeTime"></div>
                <div class="totalTime">
                    <span>已修习1天222分钟</span>
                    <span class="goPrictice">
                        <span class="orange">去修炼</span>
                        <span class="goIcon" style="transform: scale(0.8)">
                            <img src="../assets/img/goIcon.png">
                        </span>
                    </span>
                </div>
            </div>
        </div>
    </div>
    </div>
</template>

<script>
export default {
  name: 'pricticeDetails',
  data () {
    return {
      days: // ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
      [{day: '周一', num: 1}, {day: '周二', num: 2}, {day: '周三', num: 3}, {day: '周四', num: 4}, {day: '周五', num: 5}, {day: '周六', num: 6}, {day: '周日', num: 7}],
      thisweek: [{time: 24}, {time: 1}, {time: 16}, {time: 48}]
    }
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    share () {
      console.log('share')
    }
  }
}
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
    font-size: 0.48rem;
  }

  .header {
    width: 100%;
    height: 1.5rem;
    display: flex;
    justify-content: space-between;
    position: fixed;
    top: 0;
    left: 0;
    font-size: 0.48rem;
    background: #fff;
  }

  .header span {
    display: flex;
    justify-content: center;
    padding: 0.5rem;
    line-height: 0.5rem;
  }

  .goIcon,
  .back {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 0.5rem;
    width: 0.266667rem;
  }

  .share {
    height: 0.5rem;
    width: 0.4322rem;
  }

  img {
    width: 100%;
  }

  .user {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 1.5rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .userName {
    font-size: 0.42667rem;
    color: #333333;
  }

  .pricticeInfo {
    width: calc(100% - 4rem);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 1rem;
  }

  .num {
    color: #ff8309;
    font-size: 0.8rem;
  }

  .time {
    color: #666666;
  }

  .detail {
    color: #333333;
  }

  .time,
  .detail {
    font-size: 0.42667rem;
  }

  .border {
    width: 100%;
    height: 0.32rem;
    background: #f5f5f5;
    margin-top: 0.52rem;
  }

  .allRecord,
  .pricticeData {
    margin-top: 0.37333rem;
  }

  .title {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    text-align: start;
    font-size: 0.42667rem;
    color: #333333;
  }

  .dataHead {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 0.32rem;
  }

  .dataHead span {
    font-size: 0.42667rem;
    color: #333333;
  }

  .dataArea {
    width: 100%;
    height: 5.33333rem;
    background: #f5f5f5;
    margin-top: 0.37333rem;
    position: relative;
  }

  .unit {
    position: absolute;
    right: 0.4rem;
    top: 0.21333rem;
    font-size: 0.34667rem;
    color: #666666;
  }

  .weeks {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    margin-top: 0.32rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style: none;
  }

  .week {
    font-size: 0.4rem;
    color: #333333;
  }

  .dataNum {
    position: absolute;
    bottom: 0;
    background: #ffd200;
    width: 0.24rem;
    border-top-left-radius: 0.12rem;
    border-top-right-radius: 0.12rem;
    list-style: none;
  }

  .dataCount {
    display: flex;
    width: 0.5rem;
    height: 0.4rem;
    justify-content: center;
    align-items: center;
    margin-left: -0.13rem;
    margin-top: -0.50667rem;
    font-size: 0.34667rem;
  }

  .record {
    width: calc(100% - 0.8rem);
    margin: 0.4rem auto;
    height: 2.4rem;
    border-radius: 0.15rem;
    box-shadow: #f5f5f5 0 0 0.4rem;
  }

  .content {
    width: calc(100% - 0.8rem);
    margin: 0 auto;
    text-align: start;
  }

  .contentTitle {
    padding-top: 0.3rem;
    color: #333333;
    font-size: 0.4rem;
  }

  .totalTime {
    margin-top: 0.3rem;
    color: #666666;
    font-size: 0.34667rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .totalTime span {
    font-size: 0.34667rem;
  }

  .pricticeTime {
    width: 100%;
    height: 0.24rem;
    background: #f5f5f5;
    margin-top: 0.3rem;
    border-radius: 0.12rem;
  }

  .goPrictice {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .orange {
    color: #ff8309;
    margin-right: 0.24rem;
  }
</style>



